#sec2 {
  background: #f2f2f2;
  padding:5rem 0;
}
#sec2 .slider {
  width:60%;
  margin:0rem auto;
  position: relative;
}
#sec2 .slider h2 {
  margin-bottom:3vw;
  text-align: center;
}
#sec2 .slider ul {
  position: relative;
  font-size:0;
  box-shadow:0 5px 10px #cccccc;
}
#sec2 .slider ul li {
  display: inline-block;
  transition:opacity .5s ease;
  opacity:0;
  position: relative;
  position: absolute;
  width:100%;
}
#sec2 .slider ul li.imgActive {
  opacity:1;
}
#sec2 .slider ul li img {
  width:100%;
}
#sec2 .slider ul li .description {
  font-size:1.2vw;
  background: rgba(0,0,0,.9);
  color:white;
  position: absolute;
  bottom:0;
  width:100%;
  padding:.8vw 0;
  text-indent: 1.2vw;
  pointer-events: none;
  transition: opacity .5s ease;
}
#sec2 .slider .buttons {
  text-align: center;
  margin-top:1.5vw;
  position: absolute;
  top:50%;
  right:0%;
  transform:translate(300%,-50%);
  z-index: 2;

}
#sec2 .slider .buttons .buttonItem {
  width:.8vw;
  height:.8vw;
  background: black;
  border-radius:100%;
  cursor: pointer;
  margin:.5vw 0;
}
#sec2 .slider .buttons .buttonItem.buttonItemActive {
  background: #d82750;
}
@media only screen and (max-width: 768px) {
  #sec2 .slider {
    width:90%;
  }
  #sec2 .slider .buttons{
    position: relative;
    transform:translate(0%,0%);
    margin-top:10vw;
  }
  #sec2 .slider .buttons .buttonItem {
    width:3vw;
    height:3vw;
    margin:0 1vw;
    display: inline-block;
  }
}
