@import url(https://fonts.googleapis.com/css?family=Public+Sans:300,400,700,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:300,400,700&display=swap);
* {
  padding:0;
  margin:0;
  font-family: 'Public Sans', sans-serif;
  box-shadow: none;
}
html {
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth;
}
ul,li,ol {
  list-style: none;
  padding:0;
  margin:0;
}

a {
  text-decoration: none;
  color:black;
}

.korean {
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  opacity:.7;
}
section {
  position: relative;
}

.textwrap {
  position: absolute;
  top:50%;
  left:50%;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
  width:40%;
  -webkit-transition:-webkit-transform .5s ease;
  transition:-webkit-transform .5s ease;
  transition:transform .5s ease;
  transition:transform .5s ease, -webkit-transform .5s ease;
}

.textwrap h1 {
  font-size:2vw;
  margin-bottom:1.8vw;
}
.textwrap p {
  font-size:1.2vw;
  line-height: 180%;
}

@media only screen and (max-width: 768px) {
  .textwrap {
    width:90%;
  }
  .textwrap h1 {
    font-size:8vw;
    margin-bottom:1.8vw;
  }
  .textwrap p {
    font-size:4vw;
    line-height: 180%;
  }
}

.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease;
  transition: opacity 500ms ease;
}

header {
  background: #100f0f;
  position: fixed;
  width:100%;
  z-index: 99;
  border-bottom:.1px solid gray;
}
header ul {
  margin:0 auto;
  width:90%;
  text-align: center;
}

header ul li {
  display: inline-block;
  font-size:.9vw;
}
header ul li a {
  color:white;
  padding:.6rem 2rem;
  display: inline-block;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.navItem.active {
  pointer-events: none;
  background: #d82750;
}
#TopButton {
  width:3vw;
  height:3vw;
  background: black;
  position: fixed;
  right:2.5%;
  bottom:10%;
  z-index: 99;
  border-radius:10%;
  cursor: pointer;
  color:white;
  text-align: center;
  line-height: 3vw;
  font-size:1.2vw;
}

@media only screen and (max-width: 768px) {
  header {
    text-align: right;
  }
  header #menu {
    width:8vw;
    margin-top:2.5%;
    margin-right:5%;
  }
  .mobileNav {
    height:100vh;
    width:100%;
    background: #100f0f;
    position: fixed;
    top:0%;
    -webkit-transform:translate(100%,0%);
            transform:translate(100%,0%);
    z-index: 99;
    -webkit-transition: -webkit-transform .5s ease;
    transition: -webkit-transform .5s ease;
    transition: transform .5s ease;
    transition: transform .5s ease, -webkit-transform .5s ease;
  }
  .mobileNav img {
    position: absolute;
    width:8vw;
    top:2.5%;
    right:5%;
  }
  .mobileNav .liwrap {
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform:translate(-50%,-50%);
            transform:translate(-50%,-50%);
    text-align: center;
    width:100%;
  }
  .mobileNav li {
    width:100%;
    font-size:10vw;
    margin:4vw 0;
  }
  .mobileNav li a {
    width:100%;
    display: inline-block;
    color:white;
  }
}

footer {
  width:100%;
  background: black;
  color:white;
  text-align: center;
}

footer p {
  display: inline-block;
  padding:1.2vw 1.5vw;
  font-size:.9vw;
  text-indent: 3vw;
}
@media only screen and (max-width: 768px) {
  footer {
    text-align: left;
    padding:5vw 0;
  }
  footer p {
    display: block;
    padding:1.2vw 1.5vw;
    font-size:4vw;
  }
}

#sec2 {
  background: #f2f2f2;
  padding:5rem 0;
}
#sec2 .slider {
  width:60%;
  margin:0rem auto;
  position: relative;
}
#sec2 .slider h2 {
  margin-bottom:3vw;
  text-align: center;
}
#sec2 .slider ul {
  position: relative;
  font-size:0;
  box-shadow:0 5px 10px #cccccc;
}
#sec2 .slider ul li {
  display: inline-block;
  -webkit-transition:opacity .5s ease;
  transition:opacity .5s ease;
  opacity:0;
  position: relative;
  position: absolute;
  width:100%;
}
#sec2 .slider ul li.imgActive {
  opacity:1;
}
#sec2 .slider ul li img {
  width:100%;
}
#sec2 .slider ul li .description {
  font-size:1.2vw;
  background: rgba(0,0,0,.9);
  color:white;
  position: absolute;
  bottom:0;
  width:100%;
  padding:.8vw 0;
  text-indent: 1.2vw;
  pointer-events: none;
  -webkit-transition: opacity .5s ease;
  transition: opacity .5s ease;
}
#sec2 .slider .buttons {
  text-align: center;
  margin-top:1.5vw;
  position: absolute;
  top:50%;
  right:0%;
  -webkit-transform:translate(300%,-50%);
          transform:translate(300%,-50%);
  z-index: 2;

}
#sec2 .slider .buttons .buttonItem {
  width:.8vw;
  height:.8vw;
  background: black;
  border-radius:100%;
  cursor: pointer;
  margin:.5vw 0;
}
#sec2 .slider .buttons .buttonItem.buttonItemActive {
  background: #d82750;
}
@media only screen and (max-width: 768px) {
  #sec2 .slider {
    width:90%;
  }
  #sec2 .slider .buttons{
    position: relative;
    -webkit-transform:translate(0%,0%);
            transform:translate(0%,0%);
    margin-top:10vw;
  }
  #sec2 .slider .buttons .buttonItem {
    width:3vw;
    height:3vw;
    margin:0 1vw;
    display: inline-block;
  }
}

#sec1 {
  position: relative;
  overflow: hidden;
}

#sec1 video {
  width:100%;
}

#sec1 .textwrap.appearActive {
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
}
#sec1 .textwrap.appear {
  -webkit-transform:translate(-50%,0%);
          transform:translate(-50%,0%);
}

#sec3 {
  height:90vh;
  background:url(../../static/media/b2.60b69a0e.png) no-repeat 50% 50%;
  background-size:cover;
}

#sec3 .textwrap {
  width:60%;
  text-align: center;
}

#sec3 .textwrap figure {
  background: #100f0f;
  color:white;
  display: inline-block;
  font-size:3.6vw;
  width:8vw;
  height:8vw;
  text-align: center;
  line-height: 8vw;
  position: relative;
  border-radius:10%;
  margin:3vw 1vw;
}
#sec3 .textwrap figure img {
  width:70%;
  position: absolute;
  top:50%;
  left:50%;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
}
#sec3 .textwrap figure figcaption {
  width:300%;
  color:black;
  font-size:1.3vw;
  position: absolute;
  bottom:-40%;
  left:50%;
  -webkit-transform:translate(-50%);
          transform:translate(-50%);
  line-height:normal;
}
span.hidden {
  opacity:0;
}

@media only screen and (max-width: 768px) {
  #sec1 {
    padding-top:10vw;
    position: relative;
    overflow: hidden;
    height:120vw;
    background: url(../../static/media/b2.60b69a0e.png) no-repeat 80% 00%;
  }
  #sec1 video {
    display: none;
  }
  #sec3{
    background:url(../../static/media/b2.60b69a0e.png) no-repeat 20% 50%;
    height:80vh;
  }
  #sec3 .textwrap {
    width:90%;
  }
  #sec3 .textwrap figure{
    font-size:12vw;
    width:25vw;
    height:25vw;
    line-height: 25vw;
    margin:10vw 1vw;
  }
  #sec3 .textwrap figure figcaption {
    width:110%;
    font-size:4vw;
    bottom:-30%;
  }
}

#sec4 {
  padding:10vw 0;
}
#sec4 .row {
  margin-bottom:6vw;
}
#sec4 .cell {
  width:50%;
  display: inline-block;
}
#sec4 .textwrap {
  position: relative;
  -webkit-transform:translate(0,0);
          transform:translate(0,0);
  left:0;
  top:0;
  float:right;
}
#sec4 .textContain {
  position: absolute;
  top:50%;
  left:0%;
  -webkit-transform:translate(5%,-50%);
          transform:translate(5%,-50%);
  width:80%;
}
#sec4 .textwrap h3{
  font-size:1.8vw;
  margin-bottom:1.8vw;
}
#sec4 .textwrap p{
  font-size:1vw;
}
#sec4 .textwrap a{
  display: inline-block;
  font-size:1.8vw;
  margin-top:1.8vw;
  text-decoration: underline;
  color:#d82750;
  font-weight: bold;
}
#sec4 .imgwrap {
  position: relative;
}
#sec4 .imgwrap img{
  position: absolute;
  top:50%;
  right:0%;
  -webkit-transform:translate(-5%,-50%);
          transform:translate(-5%,-50%);
  width:80%;
  box-shadow:0 5px 10px #cccccc;
}

@media only screen and (max-width: 768px) {
  #sec4 {
    padding-top:40vw;
  }
  #sec4 .row {
    margin-bottom:18vw;
  }
  #sec4 .cell {
    width:100%;
  }
  #sec4 .imgwrap img{
    width:90%;
    left:50%;
    -webkit-transform:translate(-50%,-50%);
            transform:translate(-50%,-50%);
  }
  #sec4 .textwrap {
    float:none;
  }
  #sec4 .textContain {
    position: relative;
    top:0%;
    left:50%;
    margin-top:5vw;
    -webkit-transform:translate(-50%,0%);
            transform:translate(-50%,0%);
    width:90%;
  }
  #sec4 .textwrap h3{
    font-size:6vw;
    margin-bottom:3vw;
  }
  #sec4 .textwrap p{
    font-size:4.5vw;
  }
  #sec4 .textwrap a{
    display: inline-block;
    font-size:5.2vw;
    margin-top:4vw;
  }
}

#sec5 {
  width:100%;
}
#sec5 .sectionHeader {
  width:100%;
  height:30vw;
  background: #100f0f;
  text-align: center;
  position: relative;
  color:white;
}
#sec5 .sectionHeader h2 {
  font-size:3vw;
  margin-bottom:1.8vw;
}

.marginBlock {
  height:2vw;
  background: #100f0f;
}
#sec5 article {
  width:50%;
  margin:0 auto;
  padding:10vw 0;
}
#sec5 article .textwrap {
  color:#100f0f;
  position: static;
  -webkit-transform:translate(0,0);
          transform:translate(0,0);
  width:100%;
}

#sec5 article .textwrap h2 {
  font-size:2vw;
  margin-bottom:.6vw;
  opacity:1;
}
#sec5 article .textwrap h3 {
  color:#d82750;
  opacity:1;
  font-size:1.5vw;
  margin-bottom:3.6vw;
}
#sec5 article .textwrap p {
  font-size:1.2vw;
  margin-bottom:4vw;
}
#sec5 article .textwrap h4 {
  font-size:1.2vw;
  margin-bottom:4vw;
}
#sec5 article .textwrap h4 span {
  display: block;
  margin-bottom:.8vw;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
}

@media only screen and (max-width: 768px) {
  .marginBlock {
    height:8vw;
  }
  #sec5 .sectionHeader {
    height:60vw;
  }
  #sec5 .sectionHeader h2 {
    font-size:8vw;
    margin-bottom:3vw;
  }
  #sec5 article {
    width:90%;
  }
  #sec5 article .textwrap h2 {
    font-size:6vw;
    margin-bottom:2vw;
  }
  #sec5 article .textwrap h3 {
    font-size:4vw;
    margin-bottom:8vw;
  }
  #sec5 article .textwrap p {
    font-size:4vw;
    margin-bottom:8vw;
  }
  #sec5 article .textwrap h4 {
    font-size:4vw;
    margin-bottom:4vw;
  }
  #sec5 article .textwrap h4 span {
    margin-bottom:1vw;
  }
}

