footer {
  width:100%;
  background: black;
  color:white;
  text-align: center;
}

footer p {
  display: inline-block;
  padding:1.2vw 1.5vw;
  font-size:.9vw;
  text-indent: 3vw;
}
@media only screen and (max-width: 768px) {
  footer {
    text-align: left;
    padding:5vw 0;
  }
  footer p {
    display: block;
    padding:1.2vw 1.5vw;
    font-size:4vw;
  }
}
