#sec5 {
  width:100%;
}
#sec5 .sectionHeader {
  width:100%;
  height:30vw;
  background: #100f0f;
  text-align: center;
  position: relative;
  color:white;
}
#sec5 .sectionHeader h2 {
  font-size:3vw;
  margin-bottom:1.8vw;
}

.marginBlock {
  height:2vw;
  background: #100f0f;
}
#sec5 article {
  width:50%;
  margin:0 auto;
  padding:10vw 0;
}
#sec5 article .textwrap {
  color:#100f0f;
  position: static;
  transform:translate(0,0);
  width:100%;
}

#sec5 article .textwrap h2 {
  font-size:2vw;
  margin-bottom:.6vw;
  opacity:1;
}
#sec5 article .textwrap h3 {
  color:#d82750;
  opacity:1;
  font-size:1.5vw;
  margin-bottom:3.6vw;
}
#sec5 article .textwrap p {
  font-size:1.2vw;
  margin-bottom:4vw;
}
#sec5 article .textwrap h4 {
  font-size:1.2vw;
  margin-bottom:4vw;
}
#sec5 article .textwrap h4 span {
  display: block;
  margin-bottom:.8vw;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
}

@media only screen and (max-width: 768px) {
  .marginBlock {
    height:8vw;
  }
  #sec5 .sectionHeader {
    height:60vw;
  }
  #sec5 .sectionHeader h2 {
    font-size:8vw;
    margin-bottom:3vw;
  }
  #sec5 article {
    width:90%;
  }
  #sec5 article .textwrap h2 {
    font-size:6vw;
    margin-bottom:2vw;
  }
  #sec5 article .textwrap h3 {
    font-size:4vw;
    margin-bottom:8vw;
  }
  #sec5 article .textwrap p {
    font-size:4vw;
    margin-bottom:8vw;
  }
  #sec5 article .textwrap h4 {
    font-size:4vw;
    margin-bottom:4vw;
  }
  #sec5 article .textwrap h4 span {
    margin-bottom:1vw;
  }
}
