#sec1 {
  position: relative;
  overflow: hidden;
}

#sec1 video {
  width:100%;
}

#sec1 .textwrap.appearActive {
  transform:translate(-50%,-50%);
}
#sec1 .textwrap.appear {
  transform:translate(-50%,0%);
}

#sec3 {
  height:90vh;
  background:url("./b2.png") no-repeat 50% 50%;
  background-size:cover;
}

#sec3 .textwrap {
  width:60%;
  text-align: center;
}

#sec3 .textwrap figure {
  background: #100f0f;
  color:white;
  display: inline-block;
  font-size:3.6vw;
  width:8vw;
  height:8vw;
  text-align: center;
  line-height: 8vw;
  position: relative;
  border-radius:10%;
  margin:3vw 1vw;
}
#sec3 .textwrap figure img {
  width:70%;
  position: absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
}
#sec3 .textwrap figure figcaption {
  width:300%;
  color:black;
  font-size:1.3vw;
  position: absolute;
  bottom:-40%;
  left:50%;
  transform:translate(-50%);
  line-height:normal;
}
span.hidden {
  opacity:0;
}

@media only screen and (max-width: 768px) {
  #sec1 {
    padding-top:10vw;
    position: relative;
    overflow: hidden;
    height:120vw;
    background: url('./b2.png') no-repeat 80% 00%;
  }
  #sec1 video {
    display: none;
  }
  #sec3{
    background:url("./b2.png") no-repeat 20% 50%;
    height:80vh;
  }
  #sec3 .textwrap {
    width:90%;
  }
  #sec3 .textwrap figure{
    font-size:12vw;
    width:25vw;
    height:25vw;
    line-height: 25vw;
    margin:10vw 1vw;
  }
  #sec3 .textwrap figure figcaption {
    width:110%;
    font-size:4vw;
    bottom:-30%;
  }
}
