header {
  background: #100f0f;
  position: fixed;
  width:100%;
  z-index: 99;
  border-bottom:.1px solid gray;
}
header ul {
  margin:0 auto;
  width:90%;
  text-align: center;
}

header ul li {
  display: inline-block;
  font-size:.9vw;
}
header ul li a {
  color:white;
  padding:.6rem 2rem;
  display: inline-block;
  transition: all .3s ease;
}
.navItem.active {
  pointer-events: none;
  background: #d82750;
}
#TopButton {
  width:3vw;
  height:3vw;
  background: black;
  position: fixed;
  right:2.5%;
  bottom:10%;
  z-index: 99;
  border-radius:10%;
  cursor: pointer;
  color:white;
  text-align: center;
  line-height: 3vw;
  font-size:1.2vw;
}

@media only screen and (max-width: 768px) {
  header {
    text-align: right;
  }
  header #menu {
    width:8vw;
    margin-top:2.5%;
    margin-right:5%;
  }
  .mobileNav {
    height:100vh;
    width:100%;
    background: #100f0f;
    position: fixed;
    top:0%;
    transform:translate(100%,0%);
    z-index: 99;
    transition: transform .5s ease;
  }
  .mobileNav img {
    position: absolute;
    width:8vw;
    top:2.5%;
    right:5%;
  }
  .mobileNav .liwrap {
    position: absolute;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%);
    text-align: center;
    width:100%;
  }
  .mobileNav li {
    width:100%;
    font-size:10vw;
    margin:4vw 0;
  }
  .mobileNav li a {
    width:100%;
    display: inline-block;
    color:white;
  }
}
