#sec4 {
  padding:10vw 0;
}
#sec4 .row {
  margin-bottom:6vw;
}
#sec4 .cell {
  width:50%;
  display: inline-block;
}
#sec4 .textwrap {
  position: relative;
  transform:translate(0,0);
  left:0;
  top:0;
  float:right;
}
#sec4 .textContain {
  position: absolute;
  top:50%;
  left:0%;
  transform:translate(5%,-50%);
  width:80%;
}
#sec4 .textwrap h3{
  font-size:1.8vw;
  margin-bottom:1.8vw;
}
#sec4 .textwrap p{
  font-size:1vw;
}
#sec4 .textwrap a{
  display: inline-block;
  font-size:1.8vw;
  margin-top:1.8vw;
  text-decoration: underline;
  color:#d82750;
  font-weight: bold;
}
#sec4 .imgwrap {
  position: relative;
}
#sec4 .imgwrap img{
  position: absolute;
  top:50%;
  right:0%;
  transform:translate(-5%,-50%);
  width:80%;
  box-shadow:0 5px 10px #cccccc;
}

@media only screen and (max-width: 768px) {
  #sec4 {
    padding-top:40vw;
  }
  #sec4 .row {
    margin-bottom:18vw;
  }
  #sec4 .cell {
    width:100%;
  }
  #sec4 .imgwrap img{
    width:90%;
    left:50%;
    transform:translate(-50%,-50%);
  }
  #sec4 .textwrap {
    float:none;
  }
  #sec4 .textContain {
    position: relative;
    top:0%;
    left:50%;
    margin-top:5vw;
    transform:translate(-50%,0%);
    width:90%;
  }
  #sec4 .textwrap h3{
    font-size:6vw;
    margin-bottom:3vw;
  }
  #sec4 .textwrap p{
    font-size:4.5vw;
  }
  #sec4 .textwrap a{
    display: inline-block;
    font-size:5.2vw;
    margin-top:4vw;
  }
}
